@import './base/base';
@import './base/settings';

*{
    margin: 0px;
}

.ace_print-margin{
    display: none;
}

.jsoneditor, .jsonviewer{
    height: calc(100vh - 120px);
}

.jsoneditor-body{
    height: calc(100vh - 200px);
    position: relative;
}

#JSON_EDITOR{
    border: 5px solid black;
    border-radius: 5px;
}

.inner-nav-bar{
    margin-top: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
}

.side-margin{
    margin-left: 3px;
    margin-right: 3px;
}
.toaster{
    position: absolute;
    top: 0;
    right: 0;
    margin: 5px;
    width: 360px;
}

.toastmessage{
    margin: 5px;
}

.jsonviewer-body {
    height: calc(100vh - 200px);
    background: white;
    position: relative;
    border: 5px solid black;
    border-radius: 5px;
    padding: 5px;
    overflow: auto;
}

.in-fullscreen-mode{
    height: 100vh;
}

.jsoneditor-body-advanced{
    height: 100%;
    border: 5px solid black;
    border-radius: 5px;
    padding: 5px;
    overflow: auto;
}

.fix-navheader{
    padding: 0px;
    margin: 0px;
}

.hide-element{
    display:none;
}

.margin-bottom-fix{
    margin-bottom: 0px;
}

.error-message{
    color: red;
}

.screenmode{
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
    z-index: 10;
}

.screenmode:hover img{
    height:22px;
}